import { Navbar } from '@/components/navbar'
// Impor tipe Anime dan AnimeDataResponse jika API mengembalikannya dalam objek
import { getTopAnime, Anime } from '@/lib/api' 
import { AnimeCard } from '@/components/anime-card'
import Link from 'next/link'

// Tambahkan definisi Tipe jika Anda menggunakan struktur respons data: { data: Anime[] }
// Jika tipe ini tidak ada di '@/lib/api', Anda harus mendefinisikannya atau mengimpornya.
// Berdasarkan error, kita asumsikan getTopAnime mengembalikan Promise<Anime[]>
// atau kita harus mengkasting/mendefinisikan tipe respons yang benar.

// Asumsi: getTopAnime mengembalikan Promise<Anime[]>, yang merupakan tipe array.
// Jika API Anda mengembalikan { data: Anime[] }, Anda perlu mendefinisikan/mengimpor:
// type AnimeDataResponse = { data: Anime[] } 
// dan gunakan const data: AnimeDataResponse = await getTopAnime(1);

export default async function TrendingPage() {
  
  // ✅ PERBAIKAN 1: Mengatasi Error 2339
  // Asumsi: getTopAnime(1) mengembalikan tipe Promise<Anime[]> (array)
  const data: Anime[] = await getTopAnime(1) 
  
  // Jika 'data' adalah array (Anime[]), properti '.data' tidak ada.
  // Jika 'data' adalah objek respons ({ data: Anime[] }), kita harus menggunakan data.data.
  // Berdasarkan error sebelumnya di page.tsx dan popular/page.tsx, kita asumsikan data adalah array.
  // Jika data.data yang dimaksud, maka tipe di atas harus berupa objek (lihat komentar di atas).
  
  // Kita HAPUS .data, dan pastikan 'animes' bertipe Anime[]
  const animes: Anime[] = data || [] 

  // Jika Anda YAKIN API mengembalikan objek { data: Anime[] }, gunakan kode ini:
  // const data: { data: Anime[] } = await getTopAnime(1) 
  // const animes: Anime[] = data.data || []
  
  // Jika Anda tidak yakin, tetap pada asumsi array (solusi di atas) dan perbaiki API Anda.

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <h1 className="text-4xl font-bold mb-2 text-foreground">Trending/Recent Anime</h1>
        <p className="text-muted-foreground mb-8">
          Discover the latest episodes and releases (based on Zoro/Consumet provider).
        </p>

        {animes.length > 0 ? (
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
            {animes.map((anime: Anime) => ( // ✅ PERBAIKAN 2: Explicitly define 'anime' type
              <Link 
                // Menggunakan ID yang tersedia untuk key dan href
                key={anime.animeId || anime.mal_id} 
                href={`/anime/${anime.animeId || anime.mal_id}`}
              >
                <AnimeCard anime={anime} />
              </Link>
            ))}
          </div>
        ) : (
          <p className="text-center text-muted-foreground">No recent anime found from the provider.</p>
        )}
      </div>
    </main>
  )
}